import { Component,Input,Output,EventEmitter} from '@angular/core';
import { NgClass } from '@angular/common';

import { Todo } from '../data-class/todo';

@Component({
    selector:"todo-list",
    template:`
      <ul class="list-unstyled" >
        <li *ngFor="let todo of todoListIn let i=index">
            <input type="checkbox" [(ngModel)]="todo.done" (click)="checkTask(i)"/>
            <span class="done-{{todo.done}}"  [ngClass]='setClasses()'
                  >{{todo.text}}</span>
        </li>
      </ul>
    `,                //"../app/todo-portal/todo-list/todo-list.component.html",
    styleUrls:['../app/todo-portal/todo-list/todo-list.component.css'],
    directives:[NgClass]
})

export class TodoListComponent{
    @Input() todoListIn:Todo[];
    @Output() check_evt=new EventEmitter<number>();
    classSet:any;
    canSave:boolean=true;
    isUnchanged:boolean=false;
    isSpecial:boolean=true;
     
     checkTask(i:number){
        this.check_evt.emit(i);
     }
     
     setClasses(){
        this.classSet={
            showHigh:this.canSave,
            redBG:!this.isUnchanged,
            showWide:this.isSpecial
        };
       return this.classSet;
    }
    
}